.sliderbox {
  width: 200px;
  justify-content: left;
  margin-left: 20px;
  border: 5px;
  border-color: salmon;
}
.sliderbox button{
  background-color: #3f51b5;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

h1,h2{
  padding-top: 16px;
  padding-left: 25px;
  margin-top: 0;
}

div h3{
  padding: 0px 20px;
}

#nav ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}


#nav li{
  float: left;
  font-size: 15px ;
}

#nav li a{
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

#nav li a:hover {
  background-color: #999;
}

.MuiButton-label{
  font-size: 15px;
  font-weight: bold;
  color: white;
}

input[type=text], select{
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  resize: vertical;
}

/* Style the label to display next to the inputs */
label {
  padding: 12px 12px 12px 0;
  display: inline-block;
}

/* Style the submit button */
input[type=submit] {
  background-color: #3f51b5;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  
}

/* Style the container */
.container {
  border-radius: 5px;
  background-color: #f2f2f2;
  color: black;
  padding: 20px;
}

.ing {
  padding: 25px;
  list-style-position: inside;
}

.steps{
  padding: 25px;
  
}

#img {
  width: 350px;
  height: auto;
  margin-left: 20px;
  
}

.logo {
  width: 75px;
  height: 75px;
  overflow: hidden;
}

#info p {
  padding: 10px;
}
